#header {
  margin-bottom: 0;
}

.shop_inner .filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  background-color: #fff;
  position: sticky;
  top: 0;
  z-index: 2;
}
.filter h2 {
  font-weight: 400;
  font-size: 24px;
}
.filter_btn {
  cursor: pointer;
  display: flex;
  align-items: center;
}
.filter span::before {
  content: "";
  display: inline-block;
  width: 24px;
  height: 24px;
  margin-right: 5px;
  background: url(../../public/img/filter_icon.png) no-repeat center/cover;
}
.aside {
  width: 191px;
  position: sticky;
  top: 100px;
  height: 100vh;
}

input[type="checkbox"] {
  -webkit-appearance: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative;
  width: 20px;
  height: 20px;
  cursor: pointer;
  outline: none !important;
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  vertical-align: middle;
}
input[type="checkbox"]::before {
  content: "\2713";
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: scale(0) translate(-50%, -50%);
  line-height: 1;
}

/* 여기가 체크표시를 위한 코드 */
input[type="checkbox"]:checked {
  background-color: #000;
  color: white;
}
.color_list input[type="checkbox"].white:checked {
  background-color: #fff;
  color: #000;
}
.color_list input[type="checkbox"].blue:checked {
  background-color: #1790cb;
  color: #fff;
}
.color_list input[type="checkbox"].red:checked {
  background-color: #c63232;
  color: #fff;
}
/* 여기가 체크표시를 위한 코드 */
input[type="checkbox"]:checked::before {
  border-radius: 4px;
  transform: scale(1) translate(-50%, -50%);
}

.aside ul {
  padding-bottom: 24px;
  border-bottom: 1px solid #e0e0e0;
}
.shoes_price ul {
  border-bottom: none;
}
.shoes_style li {
  font-weight: 500;
}

input {
  margin-right: 5px;
}

.aside ul li {
  margin-bottom: 12px;
  display: flex;
  align-items: center;
}
.shoes_style li:last-of-type {
  margin-bottom: 0;
}

.aside h3 {
  font-size: 16px;
  font-weight: 500;
  margin: 24px 0 24px 0;
}

.brand_wraper li {
  margin-bottom: 12px;
}
.brand_wraper li:last-of-type {
  margin-bottom: 0;
}
.color_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.color_list li {
  display: flex;
  flex-direction: column;
}
.color_list li input {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}
.white {
  background-color: #fff;
  border: 1px solid #a9a9a9;
}
.black {
  background-color: #000;
}
.blue {
  background-color: #1790cb;
}
.red {
  background-color: #c63232;
}

.price_wrapper ul li {
  margin-bottom: 12px;
}

.price_wrapper ul li:last-of-type {
  margin-bottom: 0;
}

.side_list {
  display: flex;
}
.list_wrapper {
  margin-left: auto;
}
.list_wrapper .list {
  display: inline-block;
  width: 25%;
  margin-bottom: 48px;
  padding: 0 12px;
  cursor: pointer;
  transition: width 0.3s;
  box-sizing: border-box;
}
.list_wrapper .list:nth-of-type(4n) {
  margin-right: 0;
}
.list_wrapper .name_price {
  margin-top: 28px;
}
.list .img_wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.list:hover .img_wrapper img {
  transform: scale(1.1);
}
