.DetailPage {
  margin-top: 54px;
}

.DetailPage .inner {
  display: flex;
}

.product_img_wrapper {
  width: 1362px;
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}
.product_img_wrapper figure {
  width: 648px;
  height: 848px;
}
.product_img_wrapper img {
  width: 100%;
  height: 100%;
}
.DetailPage .order{
  height: 100vh;
}
.order {
  margin-left: 24px;
  width: 437px;
  padding: 0 41px;
  box-sizing: border-box;
  position: sticky;
  top: 24px;
  height: 100%;
}
.order .shoes_name {
  font-size: 32px;
}
.shoes_category {
  color: #757575;
}
.shoes_price {
  margin-top: 24px;
}
.size_title {
  display: flex;
  justify-content: space-between;
  margin-top: 36px;
}
.size_title h3 {
  font-size: 16px;
}
.size_title p {
  color: #999;
}
.size_select {
  display: flex;
  flex-wrap: wrap;
  margin: 12px 0 19px 0;
}
.size_select li {
  margin-right: 5px;
  margin-bottom: 5px;
}
.size_select li:nth-child(5n) {
  margin-right: 0;
}
.size_select li input[type="checkbox"] {
  display: none;
}
.size_select input[type="checkbox"] + label {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  width: 67px;
  height: 40px;
  border: 1px solid #a9a9a9;
  position: relative;
  text-align: center;
  line-height: 38px;
  border-radius: 5px;
  cursor: pointer;
  box-sizing: border-box;
}
.size_select input[type="checkbox"]:checked + label {
  background-color: #111;
  color: #fff;
}
.detail_btn,
.detail_btn_2 {
  width: 355px;
  height: 63px;
  border-radius: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}
.detail_btn {
  background-color: #111;
  color: #fff;
  margin-bottom: 12px;
}
.detail_btn_2 {
  background-color: #fff;
  color: #000;
  border: 1px solid #a9a9a9;
}
.detail_btn span,
.detail_btn_2 span {
  width: 16px;
  height: 16px;
  display: inline-block;
  margin-left: 6px;
}
.detail_btn span {
  background: url(../../public/img/basket_icon.png) no-repeat;
}
.detail_btn_2 span {
  background: url(../../public/img/like_icon.png) no-repeat;
}
.detail_description p {
  margin: 36px 0;
  text-align: justify;
}
.color_code li {
  margin-bottom: 12px;
}
.color_code li::before {
  content: "•";
  display: inline-block;
  margin-right: 6px;
}
.color_code li:last-child {
  margin-bottom: 0;
}
