.cart_title {
  font-size: 24px;
  margin: 54px 0 36px 0;
}
.cart_order {
  display: flex;
  position: sticky;
}
.cart_list {
  width: 1346px;
}
.cart_list_none {
  width: 1346px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.cart_list table {
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
  width: 1346px;
}
.cart_list .btn {
  margin-top: 0;
}
.cart_list table tr {
  border-top: 1px solid #e0e0e0;
  padding: 12px 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
}
.cart_list table th {
  font-weight: 500;
}
.cart_list td,
.cart_list th {
  width: 332px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.cart_list .cart_btn,
.cart_list .cart_length {
  width: 114px;
}
.cart_list .cart_check {
  width: 50px;
}
.cart_list .cart_check > div input {
  margin-top: 6px;
}
.cart_information p:first-of-type {
  color: #111;
}
.cart_information p {
  margin-bottom: 6px;
  color: #656565;
  text-align: center;
}
.cart_information p:last-of-type {
  margin-bottom: 0;
}
.cart_list .img_wrapper {
  width: 130px;
  height: 130px;
}
.cart_arrive td {
  display: unset;
}
.check_delete {
  display: flex;
  margin-top: 24px;
}
.order_list {
  font-size: 32px;
  font-weight: 500;
  margin-bottom: 72px;
}
.price_delivery {
  border-bottom: 1px solid #e0e0e0;
  padding-bottom: 24px;
}
.order_price {
  margin-bottom: 12px;
}
.order_price p:nth-of-type(2) {
  font-weight: 500;
}
.total_price {
  padding: 24px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 41px;
}
.price_delivery div {
  display: flex;
  justify-content: space-between;
}
.cart_arrive td {
  padding: 12px 0;
}
.del_free {
  text-align: right;
  color: #656565;
}
.pay {
  margin-bottom: 12px;
}
.check_delete {
  display: flex;
  align-items: center;
}
.checked_del {
  margin-left: 12px;
}
.recomended {
  position: relative;
}
.recomended h3 {
  margin-bottom: 24px;
}
.swiper .img_wrapper {
  margin-bottom: 12px;
}
.detail_btn span {
  background-image: url(/public/img/pay_icon.png);
}
.cart_length > div{
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.cart_length > div p{
  margin: 0 6px;
}
.cart_length > div button{
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: #111;
  border: none;
  color: #fff;
  line-height: 0;
  text-align: center;
}
.cart_length > div button:active{
  background-color: #555;
}