#header {
  width: 100%;
  border-bottom: 1px solid #f5f5f5;
}

#header li:last-child {
  margin-right: 0;
}
#header > div .inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu_top {
  display: flex;
  justify-content: space-between;
  background-color: #f5f5f5;
}
.Logo_list {
  display: flex;
}
.Logo_list li {
  margin-right: 12px;
  padding: 0 12px;
}

.store_location li::after {
  content: "|";
  margin: 0 12px;
  font-size: 12px;
  color: #111;
}
.store_location a {
  font-size: 12px;
  line-height: 36px;
}

.store_location li:last-child::after {
  display: none;
}
.Logo {
  width: 60px;
  height: 60px;
}
.Logo svg {
  width: 100%;
  height: 100%;
}
.main_menu,
.store_location,
.icon_menu {
  display: flex;
}
.menu_bottom {
  display: flex;
  justify-content: space-between;
}
.menu_bottom li {
  margin-right: 36px;
}
.menu_bottom > * {
  flex: 1;
}
.main_menu {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.main_menu li a {
  font-weight: 500;
  line-height: 60px;
}
