.txt_wrapper span {
  font-weight: 500;
}

.chance {
  position: relative;
}
.chance .txt_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chance h2 {
  margin: 48px 0 24px 0;
}
.chance .desc {
  text-align: center;
}

.chance ul {
  display: flex;
}

.chance .btn {
  margin-right: 24px;
}
.img_wrapper_wrapper {
  position: relative;
}
.img_wrapper_wrapper .btn_2 {
  position: absolute;
  bottom: 48px;
  left: 48px;
  z-index: 10;
  cursor: pointer;
  font-weight: 500;
}
.chance .btn.btn:last-of-type {
  margin-right: 0;
}

.luka_zion > div {
  display: flex;
}

.luka_zion > div:last-of-type {
  flex-direction: row-reverse;
  margin-top: 130px;
}
.luka_zion .img_wrapper {
  width: 900px;
  height: 469px;
}

.luka_zion .txt_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.luka_zion > div div {
  flex: 1;
}
.luka_zion .txt_wrapper h2 {
  margin: 12px 0 24px 0;
}
.not_6 {
  display: flex;
}
.not_6 > div {
  flex: 1;
}
.not_6 .img_wrapper {
  width: 900px;
}

.not_6 .txt_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 81px;
}
.not_6 .txt_wrapper p {
  margin-top: 24px;
}
.jordan_row .txt_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 48px;
}
.jordan_row h2 {
  margin: 12px 0 24px 0;
}
.jordan_row .txt_wrapper .desc {
  text-align: center;
}
.special_jordan h2 {
  font-size: 24px;
  margin-bottom: 24px;
}
.special_wrapper > div:first-of-type {
  background: url(../../public/img/Special\ jordan_1.jpg) no-repeat center/cover;
}
.special_wrapper > div:last-of-type {
  background: url(../../public/img/Special\ jordan_2.jpg) no-repeat center/cover;
  margin-right: 0;
}

.special_wrapper {
  display: flex;
}
.special_wrapper > div {
  overflow: hidden;
}

.special_wrapper > div {
  flex: 1;
  height: 703px;
  margin-right: 24px;
  padding: 36px;
  box-sizing: border-box;
  display: flex;
  align-items: flex-end;
  cursor: pointer;
}
.special_wrapper [class^="btn"] {
  margin-top: 36px;
}

.special_wrapper > div:last-of-type h3 {
  color: #fff;
}

.flight {
  position: relative;
}
.flight .swiper-slide {
  display: flex;
  align-items: center;
}
.flight .swiper-slide > div {
  flex: 1;
}
.flight > h2 {
  font-size: 24px;
  margin-bottom: 24px;
}
.flight .img_wrapper {
  width: 900px;
  height: 900px;
}
.flight .txt_wrapper {
  margin-left: 81px;
}

.flight .swiper-slide h2 {
  margin-bottom: 12px;
}

.flight .desc {
  margin-top: 24px;
}

.bottom_slide {
  width: 900px;
  position: absolute;
  right: 0;
  bottom: 0;
}
.top_slide .swiper-slide {
  background-color: #fff;
}

.bottom_slide .swiper-slide {
  height: 282px;
}

.bottom_slide .img_wrapper {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
