#footer {
  background-color: #111;
  padding-bottom: 36px;
}

#footer * {
  color: #fff;
}

.footer_top {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #333;
}
.menu {
  margin: 24px 0;
  display: flex;
}
.menu li:last-child {
  margin-right: 0;
}
.footer_top span {
  display: block;
  font-weight: 600;
  margin-bottom: 6px;
}
.footer_top li {
  margin-right: 120px;
}
#footer .footer_top .menu li a {
  color: #757575;
  transition: color 0.2s;
}
#footer .footer_top .menu li a:hover {
  color: #fff;
}
.footer_bottom_top {
  display: flex;
  justify-content: space-between;
  padding-top: 36px;
}
.menu_wrapper {
  display: flex;
}
.menu_wrapper ul {
  margin-right: 60px;
}
.menu_wrapper ul:last-of-type {
  margin-right: 0;
}
.menu_wrapper li {
  margin-bottom: 12px;
}
#footer .help_menu a,
#footer .about_menu a {
  font-size: 12px;
  color: #757575;
}
#footer .help_menu a:hover,
#footer .about_menu a:hover {
  color: #fff;
}

#footer .help_menu .point a,
#footer .about_menu .point a {
  font-size: 16px;
  color: #fff;
}
.copyright {
  margin-top: 78px;
}
.copyright_top {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  border-bottom: 1px solid #757575;
  padding-bottom: 12px;
}

.reserved {
  display: flex;
}
.reserved p:first-of-type {
  margin-right: 36px;
}
.privacy {
  display: flex;
}
.privacy li {
  margin-right: 24px;
}
#footer .privacy li a,
#footer .reserved p {
  color: #757575;
}
#footer .privacy li a:hover {
  color: #fff;
}
#footer .privacy .point a,
#footer .reserved .point {
  color: #fff;
}
.privacy li:last-child {
  margin-right: 0;
}
.copyright_bottom {
  font-size: 12px;
  display: flex;
  justify-content: space-between;
  margin-top: 12px;
}
.copyright_bottom > div {
  width: 746px;
  text-align: justify;
}

#footer .copyright_bottom p {
  color: #757575;
  line-height: 1.3;
}
.copyright_bottom > div p:nth-of-type(2) {
  margin-top: 24px;
}

.copyright_bottom a {
  text-decoration: underline;
}
