video {
  width: 100%;
  height: 950px;
  object-fit: cover;
}
section {
  margin-bottom: 130px;
}
h2 {
  font-size: 48px;
  font-weight: 600;
}
h3 {
  font-size: 24px;
  font-weight: 500;
}
.txt_wrapper {
  line-height: 1.3;
}

.special_nike .txt_wrapper {
  text-align: center;
}

.special_nike h2 {
  margin: 48px 0 24px 0;
}
.special_nike .img_wrapper {
  margin: 24px 0 12px 0;
}

.leggings {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.leggings__txt_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 64px;
}
.leggings__txt_wrapper span {
  font-weight: 500;
}
.leggings h2 {
  margin: 12px 0 24px 0;
}
.btn_wrapper {
  display: flex;
}
.btn_wrapper li {
  margin-right: 24px;
}
.btn_wrapper li:last-child {
  margin-right: 0;
}

.leggings_list .list {
  display: flex;
  margin-top: 24px;
}
.leggings_list .list div {
  flex: 1;
  overflow: hidden;
  height: 740px;
  margin-right: 24px;
}
.leggings_list .list > div > div {
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
  padding: 36px;
  box-sizing: border-box;
  cursor: pointer;
  margin-right: 24px;
  transition: background 0.3s;
  position: relative;
  width: 100%;
}

.leggings_list .list > div:hover .btn_2 {
  width: 100%;
  height: 80px;
  border-radius: 40px;
}
.leggings_list .list > div:hover .btn_2 a {
  line-height: 80px;
}
.leggings_list .list div > div:hover {
  background-size: 110%;
  transition: all 0.3s;
}
.leggings_list .list > div:last-of-type {
  margin-right: 0;
}
.leggings_list .list > div h4 {
  margin-bottom: 36px;
  font-weight: 600;
  color: #fff;
}

.yoga {
  background: url(../../public/img/everyday\ leggings_1.jpg) no-repeat center / 100%;
}
.training {
  background: url(../../public/img/everyday\ leggings_2.jpg) no-repeat center / 100%;
}
.running {
  background: url(../../public//img/everyday\ leggings_3.jpg) no-repeat center / 100%;
}

.running_guide .txt_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.running_guide h2 {
  margin: 48px 0 24px 0;
}

.running_guide span {
  margin-top: 48px;
  display: block;
}
.swiper_slide {
  cursor: pointer;
}
.name_price {
  transition: transform 0.2s;
}
.swiper_slide:hover img {
  transform: scale(1.1);
}

.featured {
  position: relative;
}

.swiper-button-next,
.swiper-button-prev {
  background-color: #f5f5f5;
  border-radius: 50%;
  width: 48px;
  height: 48px;
  z-index: 3;
  border: 1px solid #e0e0e0;
  top: 0;
  position: absolute;
}
.swiper-button-prev {
  left: 1667px;
}
.swiper-button-next {
  right: 0;
}
.swiper-button-prev::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: url(/public/img/prev_btn.png) no-repeat 50%;
}
.swiper-button-next::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: url(/public/img/next_btn.png) no-repeat 50%;
}

.featured h3 {
  margin-bottom: 24px;
}
.featured .img_wrapper {
  width: 438px;
  height: 438px;
  overflow: hidden;
  margin-bottom: 12px;
}

.img_wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s;
}
.new_challenge .txt_wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.new_challenge h2 {
  margin: 48px 0 24px 0;
}

.new_challenge .btn {
  margin-top: 48px;
}

.title_wrapper {
  text-align: center;
}
.h2_desc {
  margin: 24px 0 102px 0;
}

.best_wrapper > a {
  display: flex;
  margin-bottom: 48px;
}
.best_wrapper > a:hover img {
  transform: scale(1.1);
}
.best_wrapper:last-of-type {
  margin-bottom: 0;
}
.best_wrapper a > div {
  flex: 1;
}
.best .img_wrapper {
  height: 455px;
  overflow: hidden;
}
.best .txt_wrapper {
}
.img_wrapper img {
  width: 100%;
}

.best .txt_wrapper {
  margin-left: 81px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.best .category {
  margin: 12px 0;
}
.best .desc {
  margin: 60px 0 48px 0;
}
.nike_series {
  display: flex;
}
.nike_series > div {
  margin-right: 24px;
  flex: 1;
}
.nike_series > div:hover img {
  transform: scale(1.1);
}
.nike_series > div:last-of-type {
  margin-right: 0;
}
.nike_series .img_wrapper {
  width: 100%;
  overflow: hidden;
}

.nike_series h2 {
  font-size: 32px;
  margin: 36px 0;
}

.nike_series .btn {
  margin-top: 48px;
}

.swiper {
  position: unset;
}
