body * {
  font-family: "Noto Sans KR", sans-serif;
  color: #111;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #111;
}

.inner {
  padding: 0 48px;
  width: calc(100% - 96px);
  margin: 0 auto;
}
.inner_2 {
  margin: 0 auto;
  width: calc(100% - 404px);
  padding: 0 202px;
}
.img_wrapper{
  overflow: hidden;
}
.img_wrapper img {
  width: 100%;
  height: 100%;
}
.btn {
  width: 114px;
  height: 40px;
  background-color: #111;
  color: #fff;
  border-radius: 20px;
  text-align: center;
  line-height: 40px;
  transition: all 0.3s;
  display: block;
  margin-top: 48px;
  border: none;
  cursor: pointer;
}

.btn:hover,
.detail_btn {
  background-color: #555555;
}
.detail_btn:hover {
  background-color: #555;
}
.btn_2:hover,
.detail_btn_2:hover {
  border: 1px solid #111;
  box-sizing: border-box;
}

.btn_2 {
  width: 114px;
  height: 40px;
  background-color: #fff;
  border-radius: 20px;
  text-align: center;
  line-height: 40px;
  transition: all 0.3s;
  display: block;
  border: none;
  cursor: pointer;
}
.name_price {
  display: flex;
  justify-content: space-between;
}

.shoes_name {
  margin-bottom: 12px;
}

.shoes_name,
.price {
  font-weight: 500;
}
.category {
  color: #757575;
}

.desc{
  line-height: 24px;
}
button{
  cursor: pointer;
}